import Vue from 'vue'
import VueRouter from 'vue-router'
import Inquiry from '../views/Inquiry.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/inquiry',
    name: 'inquiry',
    component: Inquiry
  },
  {
    path: '*',
    name: 'any',
    component: Inquiry
  },
  {
    path: '/member',
    name: 'member',
    component: () => import('../views/Member.vue')
  },
  {
    path: '/editor',
    name: 'editor',
    component: () => import('../views/Editor.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
