<template>
  <v-list class="list">
    <v-list-group
      v-for="link in links"
      :key="link.title"
      v-model="link.active"
    >
      <template v-slot:activator>
        <v-list-item-title class="text-subtitle-2 white--text" v-text="link.title"></v-list-item-title>
      </template>

      <v-list-item
        v-for="child in link.links"
        :key="child.title"
        style="background: #fff"
        class="list_group"
        @click="changeActive($event), $router.push(child.url)"
      >
        <v-list-item-title 
        class="text-subtitle-2 item_title item_title_active" 
        v-text="child.title" v-if="child.active">
        </v-list-item-title>

        <v-list-item-title 
        class="text-subtitle-2 item_title" 
        v-text="child.title" v-if="!child.active">
        </v-list-item-title>

      </v-list-item>
    </v-list-group>
  </v-list>
</template>

<script>
export default {
  name: "Navbar",
  methods: {
    changeActive(event) {
      // console.log(event.target)

      let titleItem = document.querySelectorAll('.item_title')
      titleItem.forEach(el => el.classList.remove('item_title_active'))
      if (event.target.classList.contains('item_title')) {
        event.target.classList.add('item_title_active')
      } else {
        if (event.target.querySelector('.item_title')) {
          event.target.querySelector('.item_title').classList.add('item_title_active')
        }
      }
    }
  },
  data: () => ({
    selectedMember: 0,
    links: [
      {
        active: true,
        links: [
          { title: 'Inquiry/Management', url: '/inquiry', active: true,},
          { title: 'Member Policy', url: '/member', active: false, },
          { title: 'Editor Subscription', url: '/editor', active: false, },
        ],
        title: 'Member Management',
      },
      {
        links: [
          { title: 'B', active: false, },
          { title: 'N', active: false, },
          { title: 'S', active: false, },
        ],
        title: 'Credit charge management',
      },
      {
        links: [{ title: 'List', active: false, }],
        title: '1',
      },
      {
        links: [{ title: 'List Item', active: false, }],
        title: '2',
      },
      {
        links: [{ title: 'List Item', active: false, }],
        title: '3',
      },
      {
        links: [{ title: 'List Item', active: false, }],
        title: '4',
      },
      {
        links: [{ title: 'List Item', active: false, }],
        title: '5',
      },
    ],
  }),
}
</script>

<style>
.list_group {
  overflow: hidden;
  margin: 0 4px;
}
.list_group:first-of-type {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.list_group:last-of-type {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.item_title {
  cursor: pointer;
}
.item_title_active {
  color: #0D47A1
}
.list .v-icon {
    display: none !important;
}
</style>
